import { Component, OnInit, ViewChild } from '@angular/core';
import { MessageService } from '../../services/message.service';
import { ClassService } from '../../services/class.service';
import { CourseService } from '../../services/course.service';
import { CodeService } from '../../services/code.service';
import {Observable} from 'rxjs/Rx';
import { DomSanitizer } from '@angular/platform-browser';
import { AceEditorModule } from 'ng2-ace-editor';

@Component({
  selector: 'app-scratch',
  templateUrl: './scratch.component.html',
  styleUrls: ['../../type/type.component.css','../../type/common.css','./scratch.component.css']
})
export class ScratchComponent implements OnInit {
  @ViewChild('editor') editor;
  text:string = "";
  options:any = {minLines: 26, maxLines: 1000, printMargin: false,fontFamily: "monospace"};

  messages: any;
  data: any;
  action: any;
  actionText: any;
  courses: any;
  index: number;
  iframeType: string;
  codeEditorType: string;
  codeResult: any;
  selectedCourse: any;
  selectedAnswer: any;
  selectedCourseContent: any;
  currentLessonNumber: any;
  thirdSiteUrl: any;
  image_big: any;
  private timer;

  constructor(private messageService: MessageService,private classService:ClassService,private courseService:CourseService,private codeService:CodeService,public sanitizer: DomSanitizer) { 
    this.timer = Observable.timer(1000,2000);
    this.timer.subscribe((t) => this.onTimeOut());  
  }

  run() {
    console.log(this.text);
    this.codeService.execute(this.codeEditorType,this.text).subscribe(    
      suc => {
        console.log(suc);
        this.codeResult = suc;
      },
      err => {
        console.log(err);
      }
    );     
  }
  /*
    ngAfterViewInit() {
        this.editor.setTheme("eclipse");

        this.editor.getEditor().setOptions({
            enableBasicAutocompletion: true
        });

        this.editor.getEditor().commands.addCommand({
            name: "showOtherCompletions",
            bindKey: "Ctrl-.",
            exec: function (editor) {

            }
        })
    }
    */

  isArray(obj : any ) {
     return Array.isArray(obj)
  }
/*
  isComponent(item:any) {
    if(item.type && item.type == 'component') {
      return true;
    }
    return false;
  }
  openComponent(item:any) {
    console.log('openComponent start');
    this.componentSelector = item.selector;
  }
  */
  openUrl(item:any) {
    this.iframeType = 'thirdSite';
    var url = item.message;
    if(item.link && item.link.zh && this.ValidURL(item.link.zh)) {
      url = item.link.zh;
    }
    this.thirdSiteUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  isUrl(item) {
    return this.ValidURL(item.link) || this.ValidURL(item.message);
  }

  ValidURL(str) {
    if(str && str.startsWith("http")) {
      return true;
    }
    return false;
  }

  enlarge_image(src) {
    this.image_big = src;
  }

  closeIframe() {
    this.iframeType = '';
  }
  unenlarge_image() {
    this.image_big = '';
  }
  openEditor(name) {
    if(name == 'scratch') {
      var url = '/works/create';
      this.iframeType = 'thirdSite';
      this.thirdSiteUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
    }
    else {
      this.iframeType = 'codeEditor';
      this.codeEditorType = name;
    }
  }
  ngOnInit() {
    this.messages = [];
    this.index = 0;
    this.iframeType = '';
    this.selectedCourse = null;
  	this.messageService.sendMessage('Scratch');
    this.classService.init().subscribe(    
      suc => {
      	this.data = suc;
      	console.log(this.data);
      },
      err => {
        console.log(err);
      }
    );   	
  }

  onTimeOut() {
  	if(this.data && (this.index <= this.data.length)) {
      if(this.actionText) {
        this.action = this.actionText;
        return;
      }
      if(this.index < this.data.length) {
        var item = this.data[this.index++];
        item.source = 'message';
        this.messages.push(item);
        if(item.action) {
          this.actionText = item.action;
        }      
      }

  	}
  }

  selectCourse(course) {
    var id = course._id;
    this.selectedCourse = course;
    
  }
  closeCourseDetail() {
    this.selectedCourse = null;
  }

  startCurrentLesson() {
    this.data = this.selectedCourseContent[this.currentLessonNumber];
  }

  checkAnswer() {
    var currentQuestion = this.data[this.index-1];
    console.log('index='+this.index);
    console.log('currentQuestion==');
    console.log(currentQuestion);
    var answer = currentQuestion.answer;
    if(answer == this.selectedAnswer) {
      console.log('rignt');
      var item = currentQuestion.correct[0];
      this.data.splice(this.index, 0, item);
      this.actionText = '';      
    }
    else {
      console.log('wrong');
      var item = currentQuestion.wrong[0];
      this.data.splice(this.index-1, 0, item);
      this.index = this.index - 1;
      this.actionText = '';
    }
    this.selectedAnswer = '';
  }

  startSelectedCourse() {
    //console.log(this.selectedCourse);
    var id = this.selectedCourse._id;
    this.closeCourseDetail();
    this.actionText = '开始学习';
    this.courseService.details(id).subscribe(    
        suc => {
          this.selectedCourseContent = JSON.parse(suc['json']);
          //console.log(suc);
        },
        err => {
          console.log(err);
        }
    );     
  }
  nextStep() {
    console.log("next Step");
    if(this.actionText == '点击选择课程') {
      this.iframeType = 'courseList';
      this.courseService.list().subscribe(    
        suc => {
          this.courses = suc;
        },
        err => {
          console.log(err);
        }
      ); 
      this.actionText = '';  
    }

    else {
      if(this.actionText == '开始学习') {
        this.index = 0;  
        this.currentLessonNumber = 1;
        this.startCurrentLesson();      
      }
      if(this.actionText == '打卡，进入下一节！') {
        this.index = 0;  
        this.currentLessonNumber ++;
        this.startCurrentLesson();    
      }
      this.iframeType = '';
      var item = {
        source: 'answer',
        message: this.actionText
      };
      this.messages.push(item);
      this.action = '';
      this.actionText = '';    
    }


  } 	
}  





