export class SectionModel {
	_id: number;
    course_id: number;
    name: any;
    content: any;
    constructor(course_id:number,name:any,content:any) {
    	this._id = 0;
        this.course_id = course_id;
    	this.name = name;
    	this.content = content;
    }
}


