import { Injectable } from '@angular/core';

@Injectable()
export class AssetService {
  audio:any;

  constructor() { this.audio = new Audio(); }

  playvoice(path) {
    this.audio.src = path;
    this.audio.load();
    this.audio.play();  
  }
  pause() {
  	this.audio.pause();
  }

}



