import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CourseModel } from '../models/course.model';
@Injectable()
export class CourseService {

  constructor(private http: HttpClient) { }
  create(model:CourseModel) {
    return this.http.post('/api/course', model);
  }
  update(id:number,model:CourseModel) {
    return this.http.put('/api/course/'+id, model);
  }

  delete(id:number) {
  	return this.http.delete('/api/course/'+id);
  }

  list() {
    return this.http.get('/api/course');  
  }

  details(id) {
    //return this.http.get('/api/course/' + id);    
    return this.http.get('/static/Course_' + id + '.json'); 
  }  
}



