import { Component, OnInit, EventEmitter } from '@angular/core';
import { UploadOutput, UploadInput, UploadFile, humanizeBytes, UploaderOptions } from 'ngx-uploader';
import { WorkService } from '../../services/work.service';

@Component({
  selector: 'app-worksupload',
  templateUrl: './worksupload.component.html',
  styleUrls: ['../../type/common.css','./worksupload.component.css']
})
export class WorksuploadComponent implements OnInit {
  uploadInput: EventEmitter<UploadInput>;
  uploadImgType = 'file';
  model: any = {};
  currentUser: any;
  constructor(private workService: WorkService) { }

  ngOnInit() {
    this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
    this.model.user_id = this.currentUser._id;
  	this.uploadInput = new EventEmitter<UploadInput>();
  }
  confirm() {
    this.workService.create(this.model)
        .subscribe(
            data => {
              console.log(data);
            },
            error => {
                console.log(error);
            });  
  }
  onUploadOutput(output: UploadOutput): void {
    var data={path:'/assets/work'};
    if (output.type === 'allAddedToQueue') { 
       const event: UploadInput = {
         type: 'uploadAll',
         url: '/api/file/upload',
         method: 'POST',
         data: data
       };
       this.uploadInput.emit(event);
    } 
    else if(output.type === 'done' && typeof output.file !== 'undefined') {
      console.log('output is:');
      console.log(output);
      var response = output.file.response;
      if(this.uploadImgType == 'file') {
        this.model.file = response.filepath;
      }
      else {
        this.model.image = response.filepath;
      }
    }  
  } 
  uploadFile() {
    console.log('uploadFile');
    this.uploadImgType = 'file';
  }
  uploadImage() {
    console.log('uploadImage');
    this.uploadImgType = 'image';
  }
}



