// core/navbar.component.ts
import * as config from '../../../server/common/Config.json';
import { Component } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { MessageService } from '../services/message.service';
import { User } from '../models/user.model';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['navbar.component.css'],
})
export class NavbarComponent { 
  selectedItem = 'Home';
  subscription: Subscription;
  topnav_responsive :any;
  currentUser: User;
  ENV :string;
  constructor(private messageService: MessageService) {
        // subscribe to home component messages
      this.ENV = (<any>config).ENV;
      this.topnav_responsive = false;
      this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
      this.subscription = this.messageService.getMessage().subscribe(message => { 
        this.selectedItem = message.text; 
      });
  }  
  listClick(event, newValue) {
  	console.log(newValue);
  	this.selectedItem = newValue;
  }
  myFunction() {
    this.topnav_responsive = !this.topnav_responsive;
  }
}


