import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class CodeService {

  constructor(private http:HttpClient) { }
  execute(codeType,text) {
    var model = {
    	codeType:codeType,
    	text:text
    };
  	return this.http.post('/api/code/exec', model);
  }
}



