import { Component, OnInit } from '@angular/core';
import { MessageService } from '../services/message.service';
import { AssetService } from '../services/asset.service';
import { WordService } from '../services/word.service';

@Component({
  selector: 'app-type',
  templateUrl: './type.component.html',
  styleUrls: ['./type.component.css','./common.css'],
    host: {
        '(document:keydown)': 'handleKeyboardEvents($event)'
    }  
})
export class TypeComponent implements OnInit {
  index_of_words_set:number;
  words_set:any;
  words:any;
  word_array:any;
  word_array_result:any;
  current_index:number;
  finger_image:string;
  accuracy:number;
  speed:number;
  score_star:number;
  score_star_array:any;
  finished:any;
  timeout:any;
  score_data:any;
  beat_users:any;
  progress:string;
  startTimeStamp:any;
  type_detail:any;
  
  constructor(private messageService: MessageService,private assetService: AssetService,private wordService: WordService) { }

  getFingerImageByKey(key:any) {
  	var image = '/assets/hands/1_space.png';
  	if(key != ' ') {
  		image = '/assets/hands/1_' + key + '.png';
  	}
  	return image;
  }

  startTest(i) {
    console.log('i=' + i);
    window.scrollTo(0, 0);
  	this.startTimeStamp = 0;
  	this.score_data = 0;
  	this.beat_users = 0;
  	this.score_star = 0;
  	this.progress = '';
  	this.finished = false;
  	
  	this.word_array_result = [];
  	this.current_index = 0;
  	this.accuracy = 100;
  	this.speed = 0;
  	this.index_of_words_set = i;
    this.words = this.words_set[this.index_of_words_set];
    this.word_array = this.words.split('');
  	this.finger_image = this.getFingerImageByKey(this.word_array[this.current_index]);
  	this.type_detail = true;
  	console.log(this.finished);
  }

  ngOnInit() {
  	
  	this.type_detail = false;

    this.wordService.getLevel(1).subscribe(    
      suc => {
        this.words_set = suc;
        console.log(this.words_set);
      },
      err => {
        console.log(err);
      }
    );  

  	this.messageService.sendMessage('Type');
  }

  getRandomInt(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
  }  
  handleKeyboardEvents(event: KeyboardEvent) {
    if(this.type_detail) {
    	event.preventDefault();
        var key = event.key;
        console.log(key);

        if(this.finished) {
        	if(key == 'Enter') {
        		this.startTest((this.index_of_words_set+1)%this.words_set.length);
        	}
        	return;
        }

        if(key.toLowerCase() == this.word_array[this.current_index].toLowerCase()) {
        	console.log('right');
        	this.word_array_result[this.current_index] = 1;
        }
        else {
        	console.log('wrong');
        	this.word_array_result[this.current_index] = -1;
        	this.accuracy = Math.floor(this.accuracy - 100/this.word_array.length);
        }
        this.current_index ++;
        
        this.assetService.playvoice('/assets/voice/press2.mp3');
        this.finger_image = this.getFingerImageByKey(this.word_array[this.current_index]);
        if(!this.startTimeStamp) {
          this.startTimeStamp = Math.floor(Date.now());
        }
        else {
          this.timeout = (Math.floor(Date.now()) - this.startTimeStamp)/1000;
          this.speed = Math.floor(this.current_index/(this.timeout/60));
        }
        if(this.current_index == this.word_array.length) {
        	console.log('finished!');
        	this.finished = true;
        	this.score_data = this.getRandomInt(1000,5000);
        	this.beat_users = this.getRandomInt(500,2000);
        	this.score_star = this.getRandomInt(2,4);
        	this.score_star_array = [];
        	for(var i=0;i<this.score_star;i++) {
        		this.score_star_array.push(i);
        	}
        }
        this.progress =  this.current_index* 795/(this.word_array.length) + 'px';
        console.log('progress = ' + this.progress);
    }
    else {

    }
  }
}



