import { Component,Input,Output,EventEmitter, OnInit } from '@angular/core';
import { UploadOutput, UploadInput, UploadFile, humanizeBytes, UploaderOptions } from 'ngx-uploader';

@Component({
  selector: 'img-edit',
  templateUrl: './img-edit.component.html',
  styleUrls: ['./img-edit.component.css']
})
export class ImgEditComponent implements OnInit {
  uploadInput: EventEmitter<UploadInput>;
  @Output() public save:EventEmitter<any> = new EventEmitter();
  private _value = '';
  private _height = '';
  private _path = '';

  constructor() { }

  ngOnInit() {
    this.uploadInput = new EventEmitter<UploadInput>();
  }

  @Input()
  set value(value: string) {
    this._value = (value && value.trim()) || '<no name set>';
  }

  get value(): string { return this._value; }  

  @Input()
  set path(path: string) {
    this._path = (path && path.trim()) || '<no name set>';
  }

  get path(): string { return this._path; }  

  @Input()
  set height(height: string) {
    this._height = (height && height.trim()) || '';
    console.log('this._height in set=' + this._height);
  }

  get height(): string { 
    console.log('this._height in get=' + this._height);
  	return this._height; 
  }    

  onUploadOutput(output: UploadOutput): void {
    var data={path:this.path};
    console.log('data=');
    console.log(data);
    if (output.type === 'allAddedToQueue') { 
       const event: UploadInput = {
         type: 'uploadAll',
         url: '/api/file/upload',
         method: 'POST',
         data: data
       };
       this.uploadInput.emit(event);
    } 
    else if(output.type === 'done' && typeof output.file !== 'undefined') {
      console.log('output is:');
      console.log(output);
      var response = output.file.response;
      var filepath = response.filepath;
      console.log('filepath=' + filepath);
      this.save.emit(filepath);
    }  
  }  

}



