import { Component, OnInit,EventEmitter } from '@angular/core';
import { MessageService } from '../../services/message.service';
import { CourseService } from '../../services/course.service';
import { SectionService } from '../../services/section.service';
import { CourseModel } from '../../models/course.model';
import { SectionModel } from '../../models/section.model'; 
import { UploadOutput, UploadInput, UploadFile, humanizeBytes, UploaderOptions } from 'ngx-uploader';

@Component({
  selector: 'app-admin-course',
  templateUrl: './admin-course.component.html',
  styleUrls: ['../admin.component.css','./admin-course.component.css']
})
export class AdminCourseComponent implements OnInit {
  uploadInput: EventEmitter<UploadInput>;
  contentType = 'listCourseDetail';	
  sectionContentType = 'list';
  selectedCourse : any;
  courses : any;  
  sections: any;
  defaultImgHeight = '50px';
  defaultImgPath = '/assets/section';
  sectionsFilterList: any;
  selectedSection: any;
  selectedSectionContent: any;
  uploadImgType: any;
  selectedSectionContentIndex: number;
  selectedOptionIndex: number;
  editing = false;

  constructor(private messageService: MessageService,private courseService:CourseService,private sectionService:SectionService) { }

  edit(value) {
    this.editing = true;
  }
  onSubmit(value) {
    this.editing = false;
  }

  ngOnInit() {
    this.uploadInput = new EventEmitter<UploadInput>();
  	this.messageService.sendMessage('AdminCourse');
    this.courseService.list().subscribe(    
      suc => {
      	console.log(suc);
      	this.courses = suc;
        this.listCourse(suc[0]);          
      }
    );   
   	
  }

  saveEditable(value) {

  }

  uploadCourseImage() {
  this.uploadImgType = 'course';
  }

  editCourse() {
    this.contentType = 'editCourse';
  }

  createCourse() {
    this.contentType = 'editCourse';
    this.selectedCourse = new CourseModel('','','','',1,1000);
  }

  createSection() {
    this.contentType = 'editSection';
    this.selectedSection = new SectionModel(this.selectedCourse._id,{default:''},[]);
  }

  editSection() {
    this.contentType = 'editSection';
  }

  onSave(event) {
    console.log('event in onSave'+event);
    if(this.selectedSection) {
      this.selectedSection.name.default = event;
      console.log(this.selectedSection);
      this.sectionService.update(this.selectedSection._id,this.selectedSection).subscribe(    
          suc => {
              console.log(suc);

          },
          err => {
              console.log(err);
          }
      ); 
    }    
  }
  addSectionContent() {
    this.sectionContentType = 'add';
    this.selectedSectionContent = {};
  }

  saveSectionContent() {
    this.selectedSection.content.push(this.selectedSectionContent);
    this.sectionContentType = 'list';
  }

  deleteSection() {
    this.sectionService.delete(this.selectedSection._id).subscribe(    
      suc => {
        for(var i = this.sections.length - 1; i >= 0; i--) {
          if(this.sections[i]._id == this.selectedSection._id) {
            this.sections.splice(i, 1);
              break;
          }
        } 
      },
      err => {
        console.log(err);
      }
    ); 
  }

  saveSection() {
    if(this.selectedSection._id > 0) { // save Practice
      this.sectionService.update(this.selectedSection._id,this.selectedSection).subscribe(    
          suc => {
              console.log(suc);

          },
          err => {
              console.log(err);
          }
      ); 
    }
    else { // create Category
      this.sectionService.create(this.selectedSection).subscribe(    
          suc => {
              console.log(suc);
          },
          err => {
              console.log(err);
          }
      ); 
    }
    this.contentType = 'listCourseDetail';
  }

  markItemIndex(i) {
    console.log('i=' + i);
    this.selectedSectionContentIndex = i;
  }

  markItemOptionIndex(i,j) {
    this.selectedSectionContentIndex = i;
    this.selectedOptionIndex = j;
  }

  onSaveActive(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].active = event;
    this.saveSection();  
  }
  onSaveMessageEnglish(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].message.en = event;
    this.saveSection();
  }

  onSaveMessageChinese(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].message.zh = event;
    this.saveSection();
  }

  onSaveActionEnglish(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].action.en = event;
    this.saveSection();
  }

  onSaveActionChinese(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].action.zh = event;
    this.saveSection();
  }

  onSaveImgEnglish(event) {
    console.log('event in onSaveImgEnglish=' + event);

    this.selectedSection.content[this.selectedSectionContentIndex].img.en = event;
    this.saveSection();

  }

  onSaveImgChinese(event) {
    console.log('event=' + event);

    this.selectedSection.content[this.selectedSectionContentIndex].img.zh = event;
    this.saveSection();

  }

  onSaveLinkEnglish(event) {
    console.log('event in onSaveLinkEnglish=' + event);

    this.selectedSection.content[this.selectedSectionContentIndex].link.en = event;
    this.saveSection();

  }

  onSaveLinkChinese(event) {
    console.log('event=' + event);

    this.selectedSection.content[this.selectedSectionContentIndex].link.zh = event;
    this.saveSection();

  }


  onSaveOptionEnglish(event) {
    this.selectedSection.content[this.selectedSectionContentIndex].options.en[this.selectedOptionIndex].message = event;
    this.saveSection();
  }
  onSaveOptionChinese(event) {
    console.log('this.selectedSectionContentIndex=' + this.selectedSectionContentIndex);
    console.log('this.selectedOptionIndex=' + this.selectedOptionIndex);
    this.selectedSection.content[this.selectedSectionContentIndex].options.zh[this.selectedOptionIndex].message = event;
    this.saveSection();
  }
  navigateSection(section) {
    console.log('navigateSection');
    this.selectedSection.checked = 0;
    section.checked = 1;
    this.selectedSection = section;
    console.log(this.selectedSection);
    var content = section.content;

    for(var i=0;i<content.length;i++) {
      var item = content[i];
      if(content[i].active == undefined) {
        content[i].active = 1;
      }
      if(content[i].message && !content[i].message.default) {
        var temp = content[i].message;
        content[i].message = {default:temp,en:'__',zh:'__'};
      }
      if(!content[i].exercises && content[i].action && !content[i].action.default) {
        var temp = content[i].action;
        content[i].action = {default:temp,en:'__',zh:'__'};
      }   
      if(content[i].link && !content[i].link.default) {
        var temp = content[i].link;
        content[i].link = {default:temp,en:temp,zh:temp};
      }   
      if(content[i].img) {
        content[i].img_min = true;
      }
      if(!content[i].active) {
        content[i].active = 1;
      }      
      if(content[i].img && !content[i].img.default) {
        var temp = content[i].img;
        content[i].img = {default:temp,en:temp,zh:temp};
      }       
      if(content[i].options && !content[i].options.default) {
        var temp = content[i].options;
        content[i].options = {default:temp,en:temp,zh:temp};
      }                
    }
  }
  listCourse(course) {
    this.contentType = 'listCourseDetail';
    this.selectedCourse = course;
    if(this.sections) {
      this.sectionsFilterList = this.sections.filter((section: SectionModel) => section.course_id == course._id);
      if(this.sectionsFilterList.length > 0) {
        this.selectedSection = this.sectionsFilterList[0];
      }
    }    
    else {
        this.sectionService.list().subscribe(    
          suc => {
            console.log(suc);
            this.sections = suc;
            this.sectionsFilterList = this.sections.filter((section: SectionModel) => section.course_id == course._id);
            if(this.sectionsFilterList.length > 0) {
              this.selectedSection = this.sectionsFilterList[0];
            }
          }
        );    
    }    
    
  }  


  onUploadOutput(output: UploadOutput): void {
    var data={path:'/assets/course'};
    if (output.type === 'allAddedToQueue') { 
       const event: UploadInput = {
         type: 'uploadAll',
         url: '/api/file/upload',
         method: 'POST',
         data: data
       };
       this.uploadInput.emit(event);
    } 
    else if(output.type === 'done' && typeof output.file !== 'undefined') {
      console.log('output is:');
      console.log(output);
      var response = output.file.response;
      if(this.uploadImgType == 'course') {
        this.selectedCourse.image = response.filepath;
      }
      /*
      else {
        this.selectedPractice.image = response.filepath;
      }
      */
    }  
  }  
}




