import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { WorkModel } from '../models/work.model';
@Injectable()
export class WorkService {

  constructor(private http: HttpClient) { }
  create(model:WorkModel) {
    return this.http.post('/api/work', model);
  }
  update(id:number,model:WorkModel) {
    return this.http.put('/api/work/'+id, model);
  }

  delete(id:number) {
  	return this.http.delete('/api/work/'+id);
  }

  list() {
    return this.http.get('/api/work');   
  }

  details(id) {
    return this.http.get('/api/work/' + id);      
  }  
}



