import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Injectable()
export class SectionService {

  constructor(private http: HttpClient) { }
  create(model) {
    return this.http.post('/api/section', model);
  }
  update(id:number,model) {
    return this.http.put('/api/section/'+id, model);
  }

  delete(id:number) {
  	return this.http.delete('/api/section/'+id);
  }

  list() {
    return this.http.get('/api/section');  
  }

  details(id) {
    return this.http.get('/api/section/' + id);    
    //return this.http.get('/static/Course_' + id + '.json'); 
  }  
}



