export class CourseModel {
	_id: number;
    name: string;
    content: string;
    image: string;
    profession: string;
    active: number;
    sequence: number;
    constructor(name:string,content:string,image:string,profession:string,active:number,sequence:number) {
    	this._id = 0;
    	this.name = name;
        this.content = content;
    	this.image = image;
        this.profession = profession;
        this.active = active;
        this.sequence = sequence;
    }
}



