import { Component, OnInit } from '@angular/core';
import { MessageService } from '../services/message.service';
import { ChatService,Message } from '../services/chat.service';
import { ChatbotService } from '../services/chatbot.service';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/scan';

@Component({
  selector: 'app-chatbot',
  templateUrl: './chatbot.component.html',
  styleUrls: ['./chatbot.component.css']
})
export class ChatbotComponent implements OnInit {
  messages: Observable<Message[]>;
  formValue: string;
  chatbots: any;
  showChat = false;
  chatbotId: any;
  constructor(private messageService: MessageService,private chatService: ChatService,private chatbotService: ChatbotService) { }

  ngOnInit() {
  	this.messageService.sendMessage('Chatbot');
    this.messages = this.chatService.conversation.asObservable()
        .scan((acc, val) => acc.concat(val) );
  	console.log(this.messages);

    this.chatbotService.list().subscribe(    
      suc => {
        this.chatbots = suc;
      },
      err => {
        console.log(err);
      }
    );     
  }

  startChat(chatbotId,boolValue) {
    this.showChat = boolValue;
    this.chatbotId = chatbotId;
    this.chatService.clear();
    console.log('this.chatbotId in startChat=' + this.chatbotId);
  }

  sendMessage() {
    console.log('this.chatbotId in sendMessage=' + this.chatbotId);
    this.chatService.converse(this.chatbotId,this.formValue);
    this.formValue = '';
  }

  keyDownFunction(event) {
    if(event.keyCode == 13) {
      this.sendMessage();
    }
  }
}



