import * as config from '../../server/common/Config.json';
const ENV = (<any>config).ENV;

import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AdminComponent } from './admin/admin.component';
import { FrontendComponent } from './frontend/frontend.component';
import { GameComponent } from './game/game.component';
import { DivgameComponent } from './game/divgame.component';
import { BlocklygameComponent } from './game/blocklygame/blocklygame.component';
import { RegisterComponent } from './register/register.component';
import { CategoryComponent } from './category/category.component';
import { CategoryDetailComponent } from './category/category-detail.component';
import { PracticeComponent } from './practice/practice.component';
import { TypeComponent } from './type/type.component';
import { PracticeDetailComponent } from './practice/practice-detail.component';
import { ChatbotComponent } from './chatbot/chatbot.component';
import { LessonComponent } from './lesson/lesson.component';
import { ContactComponent } from './contact/contact.component';
import { LoginComponent } from './login/login.component';
import { ScratchComponent } from './class/scratch/scratch.component';
import { WorkslistComponent } from './works/workslist/workslist.component';
import { WorkscreateComponent } from './works/workscreate/workscreate.component';
import { WorksuploadComponent } from './works/worksupload/worksupload.component';
import { AdminTypeComponent } from './admin/admin-type/admin-type.component';
import { AdminCategoryComponent } from './admin/admin-category/admin-category.component';
import { AdminPracticeComponent } from './admin/admin-practice/admin-practice.component';
import { AdminDictionaryComponent } from './admin/admin-dictionary/admin-dictionary.component';
import { AdminChatbotComponent } from './admin/admin-chatbot/admin-chatbot.component'; 
import { AdminCourseComponent } from './admin/admin-course/admin-course.component'; 

import { AuthGuard } from './guards/auth.guard';
const routes: Routes = [
  { path: 'register', component: RegisterComponent },
  { path: 'category', component: CategoryComponent },
  { path: 'category/:id', component: CategoryDetailComponent },
  { path: 'practice/:id', component: PracticeComponent },
  { path: 'practice-detail/:id', component: PracticeDetailComponent },
  { path: 'chatbot', component: ChatbotComponent },
  { path: 'lesson', component: LessonComponent },
  { path: 'type', component: TypeComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'game', component: GameComponent },
  //{ path: 'divgame', component: DivgameComponent },
  { path: 'game/:id', component: BlocklygameComponent },
  { path: 'login', component: LoginComponent },
  { path: 'class/learn', component: ScratchComponent },
  { path: 'works/list', component: WorkslistComponent },
  { path: 'works/create', component: WorkscreateComponent },
  { path: 'works/upload', component: WorksuploadComponent, canActivate: [AuthGuard] },
  { path: '', component: FrontendComponent }
];
if(ENV == 'dev') {
  routes.push({ path: 'admin', component: AdminComponent });
  routes.push({ path: 'admin/course', component: AdminCourseComponent });
  routes.push({ path: 'admin/type', component: AdminTypeComponent });
  routes.push({ path: 'admin/practice/:id', component: AdminPracticeComponent });
  routes.push({ path: 'admin/category', component: AdminCategoryComponent });
  routes.push({ path: 'admin/chatbot', component: AdminChatbotComponent });
  routes.push({ path: 'admin/dictionary', component: AdminDictionaryComponent });
}
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [ RouterModule ],
  declarations: []
})
export class AppRoutingModule { }



