//https://angularfirebase.com/lessons/chatbot-in-angular-with-dialogflow-api-ai/

import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { DialogService } from './dialog.service';
import { HttpClient } from '@angular/common/http';

export class Message {
  constructor(public chatbotId: number, public content: string, public sentBy: string) {}
}

@Injectable()
export class ChatService {
  conversation = new BehaviorSubject<Message[]>([]);

  constructor(private dialogService: DialogService,private http: HttpClient) { }

  clear() {
  }

  converse(chatbotId:any,msg: string) {
    const userMessage = new Message(chatbotId,msg, 'user');
    this.update(userMessage);
    console.log('converse');

    var model = {
      chatbotId:chatbotId,
      text: msg
    };
    console.log('model=');
    console.log(model);
    return this.http.post('/api/dialog', model)
      .subscribe(    
          suc => {
            var response = suc;
            
            var speech = response["text"];
            console.log(speech);
            const botMessage = new Message(chatbotId,speech, 'bot');
            this.update(botMessage);            
          },
          err => {
              console.log(err);
          }
      );

  }
  // Adds message to source
  update(msg: Message) {
    this.conversation.next([msg]);
    console.log('conversation=');
    console.log(this.conversation);
  }


}



